.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Theater page styles */
.theaters-page {
  padding: 20px;
  background-color: #f5f5f5;
  min-height: calc(100vh - 64px);
}

.theaters-list .ant-card {
  transition: all 0.3s ease;
}

.theaters-list .ant-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0,0,0,0.2);
}

.theaters-list .ant-card-meta-title {
  color: #333;
  font-weight: 600;
}

.theaters-list .ant-card-actions {
  background-color: #f9f9f9;
}

.page-header {
  position: relative;
  overflow: hidden;
}

.page-header::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11 18c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm48 25c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm-43-7c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm63 31c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM34 90c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm56-76c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM12 86c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm28-65c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm23-11c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-6 60c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm29 22c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zM32 63c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm57-13c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5z' fill='%23ffffff' fill-opacity='0.1' fill-rule='evenodd'/%3E%3C/svg%3E");
  pointer-events: none;
  opacity: 0.4;
}

/* Filter section styles */
.filters-section {
  margin-bottom: 24px !important;
}

.filters-section .ant-input-search .ant-input-group {
  display: flex;
}

.filters-section .ant-input-search .ant-input {
  height: 40px;
  border-radius: 4px 0 0 4px;
}

.filters-section .ant-input-search .ant-input-search-button {
  height: 40px;
  border-radius: 0 4px 4px 0;
}

.filters-section .ant-select {
  width: 100%;
}

.filters-section .ant-select-selector {
  height: 40px !important;
  display: flex;
  align-items: center;
}

.filters-section .ant-btn {
  height: 40px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .theaters-page {
    padding: 10px;
  }
  
  .page-header {
    padding: 16px !important;
  }
  
  .filters-section .ant-col {
    margin-bottom: 10px;
  }
}
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.blurred-background {
  position: fixed; 
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://t4.ftcdn.net/jpg/06/89/49/95/360_F_689499531_MeYeI1VVavgYQRzz0S3JxkQ9VxzgYZQh.jpg');
  background-size: cover; 
  background-position: center; 
  filter: blur(8px); 
  z-index: -1; 
}

.App-header h1,
.App-header p {
  color: white; /* Change to white for better visibility */
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7); /* Add a shadow for better contrast */
}

.App-header .ant-form-item-label > label {
  color: white; /* Change label color to white */
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.9); /* Add shadow to labels */
}

.redirect-login p {
  font-size: 1.2rem; /* Increase font size */
  color: white; /* Change color to white for better visibility */
  margin-top: 20px; /* Add some margin for spacing */
}

.redirect-login a {
  color: #1890ff; /* Ant Design primary color for links */
  font-weight: bold; /* Make the link bold */
  text-decoration: underline; /* Underline the link */
}

.redirect-login a:hover {
  color: #40a9ff; /* Change color on hover */
}

.ant-input::placeholder {
  color: rgba(255, 255, 255, 0.7); /* Light color for placeholders */
  opacity: 1; /* Ensure the placeholder is fully opaque */
  font-weight: 500; /* Make placeholder text slightly bolder */
}

.ant-input:focus {
  border-color: #1890ff; /* Change border color on focus */
  box-shadow: 0 0 5px rgba(24, 144, 255, 0.5); /* Add a shadow effect on focus */
}

.ant-input:hover {
  border-color: #40a9ff; /* Change border color on hover */
}

.ant-input {
  background-color: rgba(255, 255, 255, 0.2); /* Slightly transparent background for input */
  color: rgb(3, 3, 3); /* Change input text color to white */
  border: 1px solid rgba(255, 255, 255, 0.5); /* Light border for input */
}

.left-section h1 {
  letter-spacing: 1px; /* Spreads the letters horizontally */
  font-size: 2rem; /* Adjust font size as needed */
}

.redirect-login a {
  color: #1890ff; /* Ant Design primary color for links */
  font-weight: bold; /* Make the link bold */
  text-decoration: underline; /* Underline the link */
}

.redirect-login a:hover {
  color: #40a9ff; /* Change color on hover */
  text-decoration: none; /* Remove underline on hover for a cleaner look */
}

/* Highlight form error messages */
.ant-form-item-explain-error {
  color: rgba(139, 0, 0, 1); /* Contrasting text color */
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  background-color: rgba(211, 47, 47, 0.4); /* Red background with 80% opacity */
  padding: 5px 10px; /* Add padding for readability */
  border-radius: 4px; /* Rounded corners */
  font-weight: bold; /* Emphasize the message */
  font-size: 0.9rem; /* Adjust font size */
  animation: fade-in 0.3s ease-in-out; /* Apply fade-in animation */
}
.ant-form-item-explain-error::before {
  content: '⚠️'; /* Add a warning icon */
  margin-right: 5px;
}

.loader-cotainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  z-index: 9;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #3498db;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

.ant-form-item-explain-error {
  animation: fade-in 0.3s ease-in-out;
}

@keyframes fade-in {
  from {
      opacity: 0;
  }
  to {
      opacity: 1;
  }
}

